@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Work+Sans:wght@800;900&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Helvetica', 'Open Sans', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{font-family:"PT Sans",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif !important;font-size:16px;color:#fff !important;background:#0d0d0d !important}h1::selection,p::selection{background:#b2c7c7;color:#f2f4f5}header{height:80px;width:100%;position:absolute;top:0px;z-index:10}header .logo{font-weight:900}.main{height:100vh;display:flex;align-items:center;justify-content:center;text-align:center;margin-bottom:20vh}.main p{letter-spacing:17px;color:#3d4144}.main h1{font-weight:900;letter-spacing:25px}.footer{background-color:#000;margin-top:20vh}.footer .wrapper{display:flex;align-items:center;font-size:0.8rem;font-weight:400;padding:10px 0}.footer .wrapper span{margin-left:40px}img{color:#fff;height:1.2rem;margin-right:10px;width:auto}@media (max-width: 600px){img{width:100%;height:auto}}.locations{position:absolute;margin-top:90vh;margin-left:-50%;color:#fff;font-size:0.8rem;font-weight:600}.locations span{font-weight:400;margin:0 32px;transition:0.3s ease-in-out}.locations span:first-child{margin-left:64px}a{color:#fff !important;text-decoration:none !important}button{all:unset;border:#fff 1px solid;padding:8px 15px;font-size:.8rem !important;letter-spacing:0.2rem;margin:20px 0 !important}.active{width:100px;height:100px;background-color:#b2c7c7}.menu-city-background{top:0;left:0;right:0;bottom:0;position:absolute;height:100%;width:100%;opacity:0.5;background-size:cover;background-repeat:no-repeat;background-position:bottom;-webkit-animation-name:backgroundEffect;animation-name:backgroundEffect;-webkit-animation-duration:30s;animation-duration:30s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;z-index:-1}.grid{height:100vh;display:flex;align-items:center}

